
import React from 'react';
import { useState } from 'react';

export default function TextForm(props){
 const handleUpClick= ()=>{
  let ntxt = text.toUpperCase();setText(ntxt);
  props.showAlert("Changed to uppercase","Success");
    //console.log("upper case was clicked")
  }
  const handlelowClick=()=>{
    let ltxt = text.toLowerCase();
    setText(ltxt);
    props.showAlert("Changed to Lowercase","Success");
  }
 const handleOnChange= (event)=>{
   // console.log("on change")
    setText(event.target.value);
    
  }
  const toClear=()=>{
    //let clr="";
    setText("");
  }
    const[text, setText]= useState('enter your text');
    
    function wordCount(str){
   const ar= str.split(' ');
   return( ar.filter(w => w !=='').length);
    }
  
return(
    <>

    <h1>{props.heading}</h1>
    
      <div className="mb-3">
  <label htmlFor = "Mytextbox" className="form-label"></label>
  <textarea className="form-control"  value={text} onChange={handleOnChange} id="Mytextbox" rows="10"></textarea>
</div>
<button className="btn-primary mx-2" onClick={handleUpClick}>CONVERT TO UPPERCASE</button>
<button className="btn-primary mx-2"onClick={handlelowClick}>CONVERT TO LOWERCASE</button>
<button className="btn-primary mx-2"onClick={toClear}>CLEAR TEXT</button>
<h1>text summary</h1>
<p>{wordCount(text)} words and {text.length} characters</p>
<h3>PREVIEW</h3>
<p>{text}</p>

    </>
  )
}

